<template>
  <div class="login-container">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>教务处登录</span>
        </div>
        <router-link class="custom-button" tag="button" to="/teacherLogin">教师登录</router-link><br><br>
        <router-link class="custom-button" tag="button" to="/studentLogin">学生登录</router-link>
      </template>
      <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px" status-icon>
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="form.password" type="password" placeholder="请输入密码" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleLogin">登录</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
      <div v-if="error" class="error-message">{{ error }}</div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
      },
      studentID: '',
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
        ]
      },
      error: '' // 用于存储错误信息
    };
  },
  methods: {
    // 提交表单并进行验证,返回学号
    handleLogin() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          try {
            axios.post("/api/leaderLogin", this.form).then((result) => {
              if (result.data.code == 1) {
                this.open2();
                this.$router.push('/studentManagement')
              } else {
                this.open4();
              }


            });
          } catch (error) {
            // 处理网络错误或其他异常
            this.error = '服务器连接失败，请稍后再试';
            console.error('登录请求失败:', error);
          }
        } else {
          console.log('验证失败');
          return false;
        }
      });

    },
    // 重置表单
    resetForm() {
      this.$refs.loginForm.resetFields();
      this.error = ''; // 清除错误信息
    },
    open2() {
      this.$message({
        message: '恭喜你，登录成功！',
        type: 'success'
      });
    },
    open3() {
      this.$message({
        message: '警告哦，这是一条警告消息',
        type: 'warning'
      });
    },
    open4() {
      this.$message.error('登录失败，请检查用户名与密码！');
    },
  }
};
</script>

<style scoped>
.custom-button {
  background-color: #409EFF;
  /* 蓝色背景 */
  color: white;
  /* 白色文字 */
  padding: 10px 20px;
  /* 内边距 */
  border: none;
  /* 无边框 */
  border-radius: 5px;
  /* 圆角 */
  cursor: pointer;
  /* 鼠标悬停时显示指针 */
  font-size: 12px;
  /* 字体大小 */
  transition: background-color 0.3s ease;
  /* 平滑过渡效果 */
  size: 0cqh;
}

.custom-button:hover {
  background-color: #0056b3;
  /* 悬停时的背景颜色 */
}

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f2f5;
}

.box-card {
  width: 400px;
  padding: 20px;
}

.card-header {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.error-message {
  color: red;
  margin-top: 10px;
  text-align: center;
}
</style>